import React from "react";
import { useNavigate } from "react-router";
import { urlToServer } from "client/configure_build";

import Label from "client/components/atoms/Label";
import Heading from "client/components/atoms/Heading";
import Input from "client/components/atoms/Input";
import Button from "client/components/atoms/Button";

import {
	SettingsAlign,
	InnerContainer,
	Form,
	HorizontalContainer,
	VerticalContainer,
	InputContainer,
} from "./styles";

export default function Settings() {
	const navigate = useNavigate();

	async function ConfirmChanges(form, formData) {
		return fetch(`${urlToServer}/api/user-settings`, {
			method: "POST",
			body: JSON.stringify(formData),
			credentials: "include",
			headers: {
				"Content-Type": "application/json",
			},
		})
			.then((response) => response.json())
			.then((data) => {
				return data;
			});
	}

	function ValidateFields(formJson) {
		if (formJson.newPassword !== formJson.confirmedNewPassword) {
			alert("Nowe hasa nie s takie same!");
			return false;
		}
	}

	async function Logout() {
		await fetch(`${urlToServer}/logout`, {
			method: "POST",
			credentials: "include",
			headers: {
				"Content-Type": "application/json",
			},
		});

		navigate("/");
		navigate(0);
	}

	async function handleSubmit(e) {
		e.preventDefault();

		const form = e.target;
		const formData = new FormData(form);
		const formJson = Object.fromEntries(formData.entries());

		if (ValidateFields(formJson) === false) return;

		let result = await ConfirmChanges(form, formJson);
		alert(result.message);

		if (result.sensitiveData) {
			Logout();
		}

		if (result.correct) {
			navigate(0);
		}
	}

	return (
		<SettingsAlign>
			<InnerContainer>
				<Form method="post" onSubmit={handleSubmit}>
					<HorizontalContainer>
						<VerticalContainer>
							<Heading level={5}>Zmie e-mail:</Heading>
							<InputContainer>
								<Label>Nowy e-mail:</Label>
								<Input
									name="userEmail"
									type="email"
									register
									fixedWidth
								></Input>
							</InputContainer>
						</VerticalContainer>
						<VerticalContainer>
							<Heading level={5}>Zmie imi:</Heading>
							<InputContainer>
								<Label>Nowe imi:</Label>
								<Input name="userName" register fixedWidth></Input>
							</InputContainer>
						</VerticalContainer>
					</HorizontalContainer>
					<VerticalContainer>
						<Heading level={5}>Zmie haso:</Heading>
						<VerticalContainer className="gap-4">
							<InputContainer>
								<Label>Nowe haso:</Label>
								<Input
									name="newPassword"
									type="password"
									minLength="8"
									register
									fixedWidth
								></Input>
							</InputContainer>
							<InputContainer>
								<Label>Potwierd nowe haso:</Label>
								<Input
									name="confirmedNewPassword"
									type="password"
									minLength="8"
									register
									fixedWidth
								></Input>
							</InputContainer>
						</VerticalContainer>
					</VerticalContainer>

					<VerticalContainer>
						<Heading level={5}>Potwierd zmiany:</Heading>
						<InputContainer>
							<Label>Wprowad aktualne haso:</Label>
							<Input
								name="providedPassword"
								type="password"
								required
								register
								fixedWidth
							></Input>
						</InputContainer>
						<Button
							type="submit"
							primary
							hover
							full
							className="h-[42px] self-end max-md:absolute bottom-4 md:mt-4"
						>
							Zapisz zmiany
						</Button>
					</VerticalContainer>
				</Form>
			</InnerContainer>
		</SettingsAlign>
	);
}
